.lusadevelopment {
    display: -webkit-flex;
    display: flex;
    font-weight: 700;
    letter-spacing: -2.44px;
    line-height: 28px;
    font-size: 40px;
    color: black;
}

.blinking-cursor-visible {
    display: -webkit-flex;
    display: flex;
    background-color: black;
    width: 2px;
    margin-left: 2px;
}

.blinking-cursor-hidden {
    display: -webkit-flex;
    display: flex;
    width: 2px;
    margin-left: 2px;
    visibility: hidden;
}

@media only screen and (min-width: 375px) {
    .lusadevelopment {
        letter-spacing: -2.75px;
        line-height: 31px;
        font-size: 45px;
    }
}

@media only screen and (min-width: 600px) {    
    .lusadevelopment {
        letter-spacing: -4.15px;
        line-height: 48px;
        font-size: 68px;
    }
    
    .blinking-cursor-visible {
        width: 3px;
        margin-left: 3px;
    }
    
    .blinking-cursor-hidden {
        width: 3px;
        margin-left: 3px;
        visibility: hidden;
    }
}

@media only screen and (min-width: 768px) {
    .logo-container {
        margin-top: 80px;
    }
    
    .logo {
        margin-left: -492px;
